/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.eltdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    padding: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .eltdf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .eltdf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;

        .eltdf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
        }

        .eltdf-cart-number {
            font-size: 12px;
            font-weight: 300;
            vertical-align: middle;
            padding-left: 2px;
        }

        &.eltdf-header-cart-icon-pack {
            
            .eltdf-cart-icon {
                font-size: 20px;
                line-height: 1em;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.eltdf-header-cart-svg-path {
            
            .eltdf-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {

        .eltdf-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.eltdf-dark-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.eltdf-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: 0;
    width: 290px;
    background-color: #000;
    color: #fff;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    @include eltdfTransition(all 0.35s cubic-bezier(.53,.01,.18,1));

    ul {
        margin: 35px 25px 37px;
        padding: 0;

        li {
            @include eltdfTableLayout();
            padding: 10px 0;
            box-sizing: border-box;

            &.eltdf-empty-cart {
                color: #fff;
                border-bottom: 0;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .eltdf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 78px;
        vertical-align: top;

        img {
            display: block;
            border-radius: 50%;
            max-height: 78px;
        }
    }

    .eltdf-item-info-holder {
        @include eltdfTableCellLayout();
        padding-left: 18px;

        .eltdf-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            color: #fff;
            text-transform: uppercase;
            box-sizing: border-box;

            &:before {
                content: "\25CF";
            }

            a:hover {
                color: rgba(255,255,255,.8);
            }
        }

        .remove {
            position: absolute;
            top: 8px;
            right: 0;
	        font-size: 13px;
	        line-height: 1;
            color: #fff;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .eltdf-quantity {
            display: inline-block;
            margin: 3px 0 0;
            font-size: 10px;
        }

        .amount {
            display: inline-block;
            margin: 3px 0 0;
            color: #fff;
            font-size: 10px;

            .woocommerce-Price-currencySymbol {
                font-size: 7px;
                vertical-align: super;
            }
        }
    }

    .eltdf-cart-bottom {
        padding: 17px 0 0;
        margin-top: 11px;
        border-top: 1px solid $default-border-color;

        .eltdf-subtotal-holder {
            position: relative;
            display: block;
            width: 100%;
            margin: 0 0 20px;
            font-size: 15px;
            font-family: $default-heading-font;

            > * {
                display: inline-block;
                vertical-align: top;
                color: #fff;
                line-height: 20px;
            }

            .eltdf-total-amount {
	            font-size: 15px;
            }
        }

        .eltdf-view-cart,
        .eltdf-checkout{
            position: relative;
            padding: 0;
            font-size: 9px;
            font-weight: 300;
	        letter-spacing: 0.12em;
            color: #fff;
            text-align: center;
            box-sizing: border-box;
            display: inline-block;

            &:after {
                content: '';
                width: 100%;
                height: 1px;
                background-color: #fff;
                position: absolute;
                left: 0;
                bottom: -3px;
                @include eltdfTransition(all .2s ease-out);
            }

            span {
                display: block;
            }

            &:hover {
                &:after {
                    height: 3px;
                    @include eltdfTransition(all .2s ease-out);
                }
            }
        }

        .eltdf-view-cart {
            float: right;
        }
    }
}

.eltdf-vertical-menu-area {

    .eltdf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */