/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .eltdf-content,
div.woocommerce {

    .products {

        .product {

            .added_to_cart {
                background: $default-heading-color;
                color: #fff;
                @include eltdfTransform(translateY(100%));
                @include eltdfTransitionTransform(.3s);
            }

            &:hover {

                .add_to_cart_button, .added_to_cart {
                    @include eltdfTransform(translateY(0));
                }
            }

            .add_to_cart_button {
                background: $default-heading-color;
                color: #fff;
                @include eltdfTransform(translateY(100%));
                @include eltdfTransitionTransform(.3s);
            }
        }
    }

    a.button,
    a.added_to_cart,
    input[type="submit"],
    button[type="submit"]:not(.eltdf-search-submit, .eltdf-woo-search-widget-button),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include eltdfButtonDefaultStyle();
        @include eltdfButtonMediumParams();
        @include eltdfButtonOutlineColor();
        overflow: hidden;
        white-space: nowrap;
        cursor: pointer;
        z-index: 3;

        &.single_add_to_cart_button {
            &:after {
                content: none;
            }
        }
    }

    a.button:not(.add_to_cart_button),
    input[type="submit"],
    button[type="submit"]:not(.eltdf-search-submit):not(.eltdf-woo-search-widget-button):not(.eltdf-search-form-button),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {

        @include eltdfAnimation(colorBackward 0.3s);

        &:before {
            @include eltdfAbsoluteHolderLayout();
            @include eltdfTransform(translateY(75%));
            @include eltdfTransitionTransform(.4s);
            height: 150%;
            background: $default-heading-color;
            content: '';
        }

        &:after {
            content: '';
            height: 3px;
            background-color: $default-heading-color;
            position: absolute;
            top: unset;
            bottom: 0;
            width: 100%;
            left: 0;
            @include eltdfTransition(all .2s ease);
        }

        &:hover {
            @include eltdfAnimation(colorForward 0.3s);

            &:before {
                z-index: -1;
                @include eltdfTransform(translateY(-100%));
            }

            &:after{
                @include eltdfTransition(all .2s ease);
                bottom: -3px;
                @include eltdfAnimation(animateLine 0.1s 0.2s forwards);
            }
        }
    }

    button[type="submit"].single_add_to_cart_button {
        @include eltdfButtonOutlineColor();
    }
    
    a.button.added {
        display: none;
    }

    @keyframes animateLine {
        0% {
            bottom: -3px;
        }
        100% {
            bottom: 0;
        }
    }

    @keyframes colorForward {
        0% {
            color: inherit;
        }
        50% {
            color: #fff;
        }
        100% {
            color: inherit;
        }
    }

    @keyframes colorBackward {
        100% {
            color: inherit;
        }
        50% {
            color: #fff;
        }
        0% {
            color: inherit;
        }
    }
}
/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */