/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    clear: both;
    
    &.widget_rating_filter,
    &.widget_shopping_cart,
    &.widget_layered_nav,
    &.widget_layered_nav_filters,
    &.widget_product_categories,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products  {
        
        ul {
            padding: 0;
            margin: 0;
            list-style: none;
            
            li {
                @include eltdfRelativeHolderLayout();
                padding: 0;
                margin: 0 0 14px;
                clear: both;
                
                &:last-child {
                    margin: 0;
                }
                
                ul {
                    margin: 14px 0 0 10px;
                }
            }
        }
    }
    
    &.widget_shopping_cart {
        
        .widget_shopping_cart_content {
            @include eltdfRelativeHolderLayout();
            
            ul {
                
                li {
                    
                    img {
                        display: block;
                        max-width: 84px;
                        margin: 0 70% 13px 0;
                    }
                    
                    .remove {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 0;
                        line-height: 1;
                        
                        &:after {
                            position: relative;
                            display: block;
                            padding: 0 3px;
                            font-family: 'Ionicons';
                            content: "\f404";
                            font-size: 30px;
                            line-height: 25px;
                        }
                    }
                }
            }
            
            .variation {
                @include eltdfRelativeHolderLayout();
                
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0;
                    
                    p {
                        margin: 0;
                    }
                }
            }
            
            .total {
                margin: 17px 0 13px;
            }
            
            .buttons {
                margin: 10px 0 0;
            }
        }
    }
    
    &.widget_layered_nav {
    
        .eltdf-widget-title {
            margin: 0 0 17px;
        }
        
        ul {
            
            li {
                margin: 2px 0;
                
                &.chosen {
                    
                    a {
                        color: $first-main-color;
                    }
                }
            }
        }
    }
    
    &.widget_layered_nav_filters {
        
        .eltdf-widget-title {
            margin: 0 0 17px;
        }
    }
    
    &.widget_price_filter {
        
        .price_slider_wrapper {
            
            .ui-widget-content {
                position: relative;
                height: 3px;
                background-color: #e5e5e5;
                
                .ui-slider-handle {
                    position: absolute;
                    top: -3px;
                    height: 9px;
                    width: 9px;
                    background-color: $default-heading-color;
                    border-radius: 50%;
                    margin: 0 0 0 -1px;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;
                    
                    &:last-child {
                        @include eltdfTransform(translateX(-50%));
                    }
                }
                
                .ui-slider-range {
                    position: absolute;
                    display: block;
                    height: 100%;
                    background-color: $default-heading-color;
                    z-index: 1;
                }
            }
        }
        
        .price_slider_amount {
            margin: 33px 0 0;
            
            .button {
                float: right;
                background-color: #fff;
                border: 1px solid $default-heading-color;
                color: $default-heading-color;
                padding: 7px 34px;
            }
            
            .price_label {
                float: left;
                padding-top: 8px;
            }
        }
    }
    
    &.widget_product_categories {
        
        ul {
            
            li {
                margin: 9px 0;
                overflow: hidden;
                font-family: $default-heading-font;
                font-weight: 400;
                font-size: 18px;

                &:first-child {
                    margin-top: 4px;
                }

                a {
                    font-family: $default-heading-font;
                    font-weight: 400;
                    font-size: 18px;
                    @include eltdfTransition(all .2s ease);

                    &:after {
                        content: '\25CF';
                        display: inline-block;
                        left: -7px;
                        position: absolute;
                        speak: none;
                        text-rendering: auto;
                        text-transform: none;
                        top: 50%;
                        font-size: 10px;
                        vertical-align: middle;
                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;
                        @include eltdfTransform(translateY(-50%));
                        @include eltdfTransition(left .2s ease-out);
                    }

                    &:hover {
                        padding-left: 15px;

                        &:after {
                            left: 0;
                        }
                    }
                }
                
                ul {
                    margin: 0 0 0 10px;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
    
        .eltdf-widget-title {
            margin: 0 0 29px;
        }
        
        ul {
            
            li {
                min-height: 110px;
                
                a {
                    
                    img {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        max-width: 85px;
                    }
                    
                    span {
                        position: relative;
                        display: block;
                        padding: 0 0 0 102px;
                        line-height: 20px;
                        box-sizing: border-box;
                    }
                }
                
                > *:not(a) {
                    margin: 0 0 0 102px;
                    line-height: 20px;
                }
                
                .star-rating {
                    position: relative;
                    
                    span {
                        max-width: 100px;
                    }
                }
                
                del,
                ins {
                    display: block;
                    
                    span {
                        color: inherit;
                    }
                }
                
                ins {
                    text-decoration: none;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .product-title {
                    color: #222;
                    font-family: $default-heading-font;
                    font-weight: 400;
                    font-size: 18px;
                    line-height: 23px;
                }
                
                .amount {
                    color: $default-heading-color;
                }
            }
        }
    }

    &.widget_products {
        .star-rating {
            display: none;
        }
    }
    
    &.widget_recently_viewed_products {
        
        ul {
            
            li {
                
                del,
                > .amount {
                    display: block;
                    margin-top: 10px;
                }
            }
        }
    }
    
    &.widget_recent_reviews {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 7px;
                    margin-bottom: 10px;
                }
            }
        }
        
        a {
            display: block;
            padding: 0 0 0 102px;
        }
        
        .reviewer {
            display: inline-block;
            vertical-align: middle;
            width: auto;
            clear: both;
        }
    }
    
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 10px;
                    margin-bottom: 12px;
                }
                
                .amount {
                    display: block;
                }
            }
        }
    }
    
    &.widget_product_tag_cloud {
        
        .tagcloud {

            a {
                border-radius: 0;
                color: $default-heading-color;
                display: inline-block;
                font-size: 10px !important;
                font-family: $default-text-font;
                font-weight: inherit;
                line-height: 30px;
                text-transform: uppercase;
                margin: 0 -1px -1px 0;
                padding: 0 9px;
                border: 1px solid $default-border-color;
                float: left;
                @include eltdfTransition(all .2s ease);

                &:hover {
                    color: #fff;
                    background-color: $default-heading-color;
                    border-color: $default-heading-color;
                    @include eltdfTransition(all .2s ease);
                }
            }
        }
    }
    
    &.widget_product_search {
        
        .woocommerce-product-search {
            @include eltdfRelativeHolderLayout();
            clear: both;
            
            > div {
                @include eltdfRelativeHolderLayout();
                border: 1px solid $default-border-color;
                box-sizing: border-box;
            }
            
            input,
            button {
                position: relative;
                display: inline-block;
                vertical-align: top;
                float: left;
                margin: 0;
                height: 54px;
                line-height: 32px;
                color: $default-text-color;
                background-color: transparent;
                border: 0;
                border-radius: 0;
                outline: 0;
                -webkit-appearance: none;
                box-sizing: border-box;
                @include eltdfTransition(color .2s ease-out);
            }
            
            input {
                width: calc(100% - 40px);
                padding: 10px 10px 10px 20px;
                font-family: inherit;
                font-size: 13px;
    
                @include eltdfPlaceholder {
                    color: inherit;
                }
            }
            
            button {
                width: 40px;
                padding: 10px 13px 10px 4px;
                font-size: 14px;
                cursor: pointer;
                
                &:hover {
                    color: $default-heading-color;
                }
                
                span {
                    display: block;
                    line-height: inherit;
                    
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }
}

.widget {

    // price filter - legacy and block common
    $slider-handle-size: 9px;
    $slider-rail-height: 3px;
    $slider-bottom-margin: 33px;

    @mixin qodefWooSliderHandle() {
        background-image: none;
        border: none;
        border-radius: 50%;
        background-color: $default-heading-color;
        height: $slider-handle-size;
        margin: 0 0 0 -1px;
        outline: none;
        position: relative;
        top: -#{($slider-handle-size - $slider-rail-height) / 2};
        width: $slider-handle-size;

        &:hover {
            @include qodefWooSliderHandleFocus();
        }
    }

    @mixin qodefWooSliderHandleFocus() {
        transform: scale(1);
        background-color: $default-heading-color;
    }

    // price filter - legacy
    &.widget_price_filter {

        .price_slider_wrapper {

            .ui-widget-content {
                border-radius: 0;
                cursor: pointer;
                height: $slider-rail-height;
                position: relative;
                //background-color: #e5e5e5;
                outline: medium none;

                .ui-slider-handle {
                    @include qodefWooSliderHandle();
                    position: absolute;
                    z-index: 2;

                    &:last-of-type {
                        transform: translateX(-#{$slider-handle-size});
                    }
                }

                .ui-slider-range {
                    background-color: $default-heading-color;
                    border-radius: 0;
                    display: block;
                    height: 100%;
                    position: absolute;
                    z-index: 1;
                }
            }
        }

        .price_slider_amount {
            align-items: center;
            display: flex;
            justify-content: space-between;
            margin: $slider-bottom-margin 0 0 0;
            position: relative;

            .clear {
                display: none;
            }
        }
    }

    // price filter - block
    .wc-block-components-price-slider {
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
        flex-wrap: wrap;
        margin: 0;

        .wc-block-components-price-slider__range-input-wrapper {
            box-shadow: none;
            height: $slider-rail-height;
            margin: calc((#{$slider-handle-size} / 2)) 0;
            width: 100%;
        }

        .wc-block-components-price-slider__range-input-progress {
            height: $slider-rail-height;
        }

        // cant comma-separate these type of selectors
        .wc-block-components-price-slider__range-input {

            &::-webkit-slider-thumb {
                @include qodefWooSliderHandle();
            }

            &::-moz-range-thumb {
                @include qodefWooSliderHandle();
            }

            &::-ms-thumb {
                @include qodefWooSliderHandle();
            }

            &:focus {

                &::-webkit-slider-thumb {
                    @include qodefWooSliderHandleFocus();
                }

                &::-moz-range-thumb {
                    @include qodefWooSliderHandleFocus();
                }

                &::-ms-thumb {
                    @include qodefWooSliderHandleFocus();
                }
            }
        }

        .wc-block-components-price-slider__controls,
        .wc-block-components-price-slider__range-text {
            margin: $slider-bottom-margin 0 0 0;
            order: 100;

            input {
                margin: 0;
                padding: 0;
                border: 0;
                background-color: transparent;
                display: inline-block;
                line-height: 26px;
                height: 26px;
                font-weight: 400;
                font-family: $default-text-font;
                float: right;
                font-size: 14px;
                color: $default-text-color;
                margin-top: 13px;
                max-width: 63px;
            }

            span {

                &:first-child {

                    &:after {
                        content: '';
                        margin: 0 3px;
                    }
                }
            }
        }

        .wc-block-components-formatted-money-amount {
            display: inline-block;
            line-height: 26px;
            height: 26px;
            font-weight: 400;
            font-family: $default-text-font;
            float: right;
            font-size: 14px;
            color: $default-text-color;
        }

        .wc-block-components-price-slider__actions {
            margin: $slider-bottom-margin 0 0;
            gap: 8px;

            button {
                @include eltdfButtonDefaultStyle();
                @include eltdfButtonMediumParams();
                @include eltdfButtonSolidColor();
                padding: 12px 22px;
                text-decoration: none;
                cursor: pointer;
                z-index: 3;

                &:hover {
                    @include eltdfButtonSolidHoverColor();
                }

                &.wc-block-components-filter-reset-button {
                    padding: 0;
                    background: unset !important;
                    color: $default-text-color !important;
                }
            }
        }
    }

    // price filter - block
    .wp-block-woocommerce-price-filter {
        margin-top: 28px;
    }

    .wp-block-woocommerce-price-filter,
    .wp-block-woocommerce-filter-wrapper {
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            position: relative;
            color: $default-heading-color;
            padding-bottom: 15px;
            border-bottom: 1px solid $default-border-color;
            margin-bottom: 20px;
            margin-top: 0;
        }
    }
    .wc-block-components-price-slider {

        .wc-block-components-price-slider__range-input-wrapper {
            background: #e5e5e5;

            &:before {
                opacity: 0;
            }
        }

        .wc-block-components-price-slider__range-input-progress {
            --range-color: $default-heading-color;
        }

        // cant comma-separate these type of selectors
        .wc-block-components-price-slider__range-input {

            &::-webkit-slider-thumb {
                background-color: $default-heading-color;
            }

            &::-moz-range-thumb {
                background-color: $default-heading-color;
            }

            &::-ms-thumb {
                background-color: $default-heading-color;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */