/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin eltdfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 0;
    width: auto;
    height: 28px;
    margin: 0;
    padding: 0 16px;
    color: #fff;
    font-size: 11px;
    line-height: 28px;
    letter-spacing: .12em;
    text-transform: uppercase;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    @include eltdfTransform(translateZ(0));
}

@mixin eltdfWooCommerceSaleStyle() {
    left: 0;
    background-color: $default-heading-color;
}

@mixin eltdfWooCommerceOutOfStockStyle() {
    right: 0;
    background-color: $default-heading-color;
}

@mixin eltdfWooCommerceNewProductStyle() {
    right: 0;
    background-color: $default-heading-color;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin eltdfWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 14px 0 0;
    line-height: 1;
    color: $default-heading-color;
    font-family: $default-heading-font;
    font-weight: 400;
    
    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
        margin: 0 8px 0 0;
        color: rgba($default-heading-color, .8);
    }
    
    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: Font Awesome\ 5 Free;
    font-size: 14px;
    line-height: 1;
    letter-spacing: 7px;
    color: $first-main-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin eltdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\f005\f005\f005\f005\f005";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\f005\f005\f005\f005\f005";
            font-weight: 900;
        }
    }
}

@mixin eltdfWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\f005";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\f005\f005\f005\f005\f005";
        font-weight: 900;
        position: absolute;
        right: 0;
        bottom: 7px;
        display: block;
        @extend %star-ratings;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */