/* ==========================================================================
   YITH WishList styles - begin
   ========================================================================== */

#yith-wcwl-popup-message {
    position: absolute; //absolute centering over added item
    margin:0 !important; //absolute centering over added item
    -webkit-transform-origin:0 0;
    transform-origin:0 0;
    @include eltdfTransform(translate(-50%,-50%));
    border: 1px solid #ebebeb;
    border-radius: 0;
    padding: 0 21px;
    line-height: 35px;

    #yith-wcwl-message {
        font-size: 14px;
        font-weight: 600;
        letter-spacing: 0.1em;

        &:before {
            position: relative;
            font-family: 'ElegantIcons';
            content: "\4e";
            top: 2px;
            right: 7px;
        }
    }
 
    //vanishing effect
    &.eltdf-wishlist-vanish-out {
      @include eltdfAnimation(eltdfWishlistvanishOut 1s both);
    }

    @keyframes eltdfWishlistvanishOut {
      0% {
        opacity: 1;
        @include eltdfTransform(scale(1,1) translate(-50%,-50%));
        -webkit-filter: blur(0px);
        filter: blur(0px);
      }

      100% {
        opacity: 0;
        @include eltdfTransform(scale(2,2) translate(-50%,-50%));
        -webkit-filter: blur(10px);
        filter: blur(10px);
      }
    }
}

.eltdf-yith-wcqv-holder {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.yith-wcwl-add-to-wishlist {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin: 0;
    background-color: #555555;
    width: 26px;
    text-align: center;
}

.yith-wcwl-add-button,
.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

    a {
        position: relative;
        display: inline-block;
        vertical-align: top;
        font-size: 0;
        line-height: inherit;
        color: #fff;

        &:before {
            content: "\e089";
            position: relative;
            display: inline-block;
            font-family: 'Font Awesome\ 5 Free';
            font-size: 11px;
            line-height: inherit;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
            @include eltdfTransition(opacity .2s);
        }

        // content: "\4e";

        &:not(.eltdf-adding-to-wishlist):hover {
            &:before {
                opacity: .8;
            }
        }

        &.eltdf-adding-to-wishlist {
            @include keyframes(eltdfWishlistLoading) {
                0%, 100% {
                    opacity:1;
                }

                50% {
                    opacity: 0.2;
                }
            }

            &:before {
                @include eltdfAnimation(eltdfWishlistLoading 1s ease infinite);
            }
        }

        &:hover {
            color: #fff;
        }
    }
}

.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

    a {
        &:before {
            content: "\4e";
        }
    }
}

.yith-wcwl-add-button {
    .ajax-loading {
        display: none !important;
    }
}

.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

    span {
        display: none;
    }

    a {

        &:before {
            color: #fff;
        }
    }
}

.woocommerce-wishlist {

    .wishlist-title{
        display: none;
    }

    .woocommerce-message,
    .woocommerce-info,
    .woocommerce-error {
        position: relative;
        display: block;
        padding: 15px 30px;
        margin: 50px 0 30px;
        line-height: 48px;
        list-style: none;
        background-color: transparent;
        border: 1px solid $default-border-color;

        > a {
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        a {
            float: right;
        }
    }

    table.wishlist_table {
        font-size: inherit;
        margin: 50px 0 100px;

        thead {
            display: none;
        }

        tbody {

            tr {
                border: 0;
                border-bottom: 1px solid $default-border-color;

                td {
                    padding: 22px 0;
                    vertical-align: middle;
                    text-align: center;

                    &.product-remove {

                        a {
                            color: $default-text-color;
                            font-size: 0;

                            &:before {
                                content: "\f404";
                                display: inline-block;
                                vertical-align: middle;
                                speak: none;
                                color: inherit;
                                font-family: "Ionicons";
                                font-size: 25px;
                                font-style: normal;
                                font-weight: normal;
                                font-variant: normal;
                                text-transform: none;
                                text-rendering: auto;
                                line-height: 1;
                                -webkit-font-smoothing: antialiased;
                                -moz-osx-font-smoothing: grayscale;
                            }

                            &:hover {
                                color: $default-heading-color;
                            }
                        }
                    }

                    &.product-thumbnail {
                        padding-left: 8px;
                        padding-right: 8px;
                        width: 92px;

                        a, img {
                            display: block;
                        }
                    }

                    &.product-name {
                        text-align: left;
                        padding-left: 20px;

                        a {
                            font-size: 14px;
                            font-weight: 600;
                            letter-spacing: 2px;
                            text-transform: uppercase;
                        }

                        .yith-wcqv-button {
                            height: 38px;
                            line-height: 38px;
                            padding: 0 28px;
                            margin: 0;
                        }
                    }

                    &.product-price{
                        font-size: 14px;
                        font-family: $default-text-font;

                        ins{
                            text-decoration: none;
                        }
                    }

                    &.product-stock-status span{
                        color: $default-text-color !important;
                    }

                    &.product-add-to-cart {

                        a {
                            display: inline-block !important;
                            background-color: transparent;
                            border: 0;
                            padding: 0;
                            margin: 0;
                            font-size: 12px;
                            font-weight: 600;
                            text-transform: uppercase;
                            letter-spacing: 2px;
                            font-family: $default-text-font;
                            color: $default-heading-color;
                            cursor: pointer;
                            line-height: 50px;
                            height: 50px;

                            &:hover{
                                background: transparent;
                                color: inherit;
                            }
                        }
                    }
                }
            }
        }

        .yith-wcwl-share{
            margin: 0;
        }
    }
}

.eltdf-single-product-summary {
    .yith-wcwl-add-to-wishlist {
        width: auto;
        background-color: transparent;
        float: right;
        margin-left: 1px;

        .yith-wcwl-add-button,
        .yith-wcwl-wishlistaddedbrowse,
        .yith-wcwl-wishlistexistsbrowse {
            a {
                -webkit-border-radius: 1px;
                -moz-border-radius: 1px;
                border-radius: 1px;
                font-size: 14px;
                line-height: 36px;
                color: #fff;
                font-weight: 400;
                vertical-align: middle;
                padding: 0 10px;
                background-color: $first-main-color;


                &:before {
                    content: "\f004";
                    font-size: 14px;
                    color: #fff;
                    font-weight: 900;
                }
            }
        }

        .yith-wcwl-add-button {
            a {
                &:before {
                    content: "\f004";
                }
            }
        }
    }
}

.eltdf-wishlist-widget-holder{
    display: inline-block;
    vertical-align: middle;

    a {
        color: $first-main-color;
        font-size: 13px;
        margin: 0 10px;

        .eltdf-wishlist-items-number {
            color: $default-text-color;
            font-family: $default-text-font;
            font-size: 12px;
        }

        &:hover {
            color: $default-text-color;
        }
    }
}

.eltdf-light-header {
    .eltdf-wishlist-widget-holder{
        a {
            color: $header-light-color;
            .eltdf-wishlist-items-number {
                color: $header-light-color;
            }

            &:hover{
                opacity: 0.8;
            }
        }
    }
}

.eltdf-dark-header {
    .eltdf-wishlist-widget-holder{
        a {
            color: $header-dark-color;
            .eltdf-wishlist-items-number {
                color: $header-dark-color;
            }

            &:hover{
                opacity: 0.8;
            }
        }
    }
}

/* ==========================================================================
   YITH WishList styles - end
   ========================================================================== */