/* ==========================================================================
   WooCommerce Pagination styles - begin
   ========================================================================== */
.eltdf-woo-pagination-holder {
    @include eltdfRelativeHolderLayout();
    margin: 0 0 66px;
    clear: both;

    .eltdf-woo-pagination-inner {
        position: relative;
        display: table;
        width: 100%;
        text-align: right;
    }
}

.woocommerce-pagination {
    position: relative;
    display: table-cell;
    vertical-align: top;
    width: 99%;
    margin: 0;
    padding: 0;

    .page-numbers {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        margin: 0;
        padding: 0;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0 3px;

            a,
            span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                margin: 0;
                padding: 0;
                background-color: $default-heading-color;
                border: 1px solid $default-heading-color;
                color: #fff;
                @include eltdfTransition(all .2s ease);
                width: 30px;
                height: 30px;
                line-height: 30px;
                text-align: center;
                font-size: 14px;

                &:hover,
                &.current {
                    color: $default-heading-color;
                    background-color: #fff;
                    @include eltdfTransition(all .2s ease);
                }
            }

            a {

                &.next,
                &.prev {
                    font-size: 0;
                    line-height: 1;
                    vertical-align: middle;

                    &:before {
                        position: relative;
                        top: 50%;
                        display: block;
                        font-family: 'ElegantIcons';
                        font-size: 22px;
                        line-height: inherit;
                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;
                        @include eltdfTransform(translateY(-50%));
                    }
                }
    
                &.prev {
        
                    &:before {
                        content: "\34";
                    }
                }
    
                &.next {
        
                    &:before {
                        content: "\35";
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Pagination styles - end
   ========================================================================== */